.app {
  max-width: 600px;
  width: 100%;
}
.app-input {
  resize: none;
  border: none;
  width: 300px;
  height: 100px;
}
@media (max-width: 645px) {
  .app-input {
    width: 100%;
  }
}
.grid-row {
  display: flex;
  width: 100%;
}
@media (max-width: 645px) {
  .grid-row {
    display: block;
  }
}
.grid-row-2 {
  flex-direction: row-reverse;
}
@media (min-width: 646px) {
  .grid-col-main,
  .grid-col-side {
    padding: 0 15px;
  }
}
.grid-col-main {
  flex-basis: 66.66666667%;
}
.grid-col-side {
  flex-basis: 33.33333333%;
}
.search-type-selection {
  padding: 20px 0;
}
@media (max-width: 645px) {
  .search-type-selection {
    display: flex;
  }
}
.search-type-selection label {
  display: block;
}
@media (max-width: 645px) {
  .search-type-selection label {
    flex-basis: 50%;
  }
}
