button {
  border: 2px solid white;
  border-radius: 2px;
  padding: 20px 40px;
  background: transparent;
  color: white;
  font-size: inherit;
  cursor: pointer;
  width: 200px;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
button:hover {
  background: white;
  color: #5499C7;
}
@media (max-width: 645px) {
  button {
    width: 100%;
    margin: 10px 0;
  }
}
